html {
  box-sizing: border-box;
} 
*, *::before, *::after {
   box-sizing: inherit;
}
img {
   display: block;
   max-width: 100%;
}

/* переменные*/
:root {
--colorGreen: #01996D;
--color-text-menu: #292929;
--color-black: #000;
--color-text: #424242;
--background1: #245852;
}

.lato-reg {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
}
.lato-med {
  font-family: 'Lato', sans-serif;
  font-weight: 500;
}
.lato-sembold {
  font-family: 'Lato', sans-serif;
  font-weight: 600;
}
.georg-reg {
  font-family: 'Georgia', sans-serif;
  font-weight: 400;
}
.georg-bol {
  font-family: 'Georgia', sans-serif;
  font-weight: 700;
}
/*header-menu*/
.contain{
  max-width: 1370px;
  margin: 0 auto;
}
.hearder-nav__row {
  min-height: 85px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-content: center;
}
.row__logo {
  width: 80px;
  height: 23px;
}
.row__menu1 {
  display: flex;
  font-size: 20px;
  color: var(--color-text-menu);
  line-height: 24px;
}
.menu__item1, .menu__item2 {
  text-decoration: none;
}
.menu__item-1{
  margin-right: 0px;
  padding: 13px 32px;
}
.menu__item-1:hover{
  color: #fff;
  background-color: var(--colorGreen);
  border-radius: 5px;
}
.menu__item-1:last-child{
  margin-right: 0;
}
.row__menu2 {
  display: flex;
  font-size: 20px;
  color: var(--color-black);
}
.menu__item-2{
  padding: 13px 50px;
}
.menu__item-2:hover{
  color: #fff;
  background-color: var(--colorGreen);
  border-radius: 5px;
}
/*Header*/
.header {
  background-image: url('../img/bg-full.png');
  background-repeat: no-repeat, no-repeat;
  background-size: cover;
  background-position: center center;
}
.header__h1 {
  padding-top: 175px;
  max-width: 440px;
  width: 100%;
  font-size: 65px;
  color: var(--colorGreen);
  margin-bottom: 30px;
}
.header__p {
  max-width: 420px;
  width: 100%;
  color: var(--color-text);
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 18px;
}
.button-header{
  padding-top: 50px;
  padding-bottom: 377px;
}
.button {
  font-family: 'Lato', sans-serif;
  text-decoration: none;
  color: #fff;
  background-color: var(--colorGreen);
  padding: 20px 50px;
  font-size: 20px;
  line-height: 25px;
  border-radius: 5px;
}
.button:hover {
  color: var(--color-text-menu);
}
/*How it*/
.how-it {
  padding-top: 170px;
}
.litle-title {
  font-size: 24px;
  line-height: 38px;
}
.how-it__content {
  display: flex;
  justify-content: space-between;
}
.how-it__title {
  max-width: 584px;
  font-size: 50px;
  line-height: 78px;
  color: var(--colorGreen);
  margin-right: 15px;
}
.how-it__text {
  max-width: 650px;
  width: 100%;
  padding-top: 20px;
  font-size: 20px;
  line-height: 30px;
  color: var(--color-text);
}
.how-it__cards {
  padding-top: 52px;
  display: flex;
  justify-content: space-between;
  padding-bottom: 51px;
}
.how-it__cards__item {
  margin-right: 20px;
}
.how-it__cards__item:last-child{
  margin-right: 0;
}
.c-item2{
  padding-top: 204px;
}
.how-it__position {
  position: relative;
}
.item__big-number {
  font-size: 150px;
  line-height: 225px;
  color: rgba(#01996D, 0.2);
}
.item__title {
  position: absolute;
  top: 135px;
  font-size: 30px;
  line-height: 45px;
  color: var(--color-black);
}
.i-title1{
  left: 90px;
}
.i-title2{
  left: 60px;
}
.i-title3{
  left: 125px;
}
.item__text {
  max-width: 382px;
  font-size: 20px;
  line-height: 30px;
  color: var(--color-text);
  margin-bottom: 60px;
}
.how-it__img3{
  overflow: hidden;
  border-radius: 12px;
}
/*Story*/
.story {
  background-color: var(--background1);
}
.contain-story {
  max-width: 1506px;
  margin: 0 auto;
}
.story__inner {
  display: flex;
  justify-content: space-between;
}
.story__left {
  margin-right: 15px;
}
.story__left__h2 {
  max-width: 550px;
  width: 100%;
  padding-top: 310px;
  font-size: 50px;
  line-height: 78px;
  color: #fff;
  margin-bottom: 33px;
}
.story__left__p {
  max-width: 555px;
  width: 100%;
  font-size: 25px;
  line-height: 39px;
  color: #fff;
}
.button-story {
  padding-top: 70px;
}
.story__right {
  margin-top: 229px;
  margin-bottom: 177px;
}
.video-place{
  width: 896px;
  height: 516px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url('../dist/img/story-img.png') 50%/cover no-repeat;
  border-radius: 8px;
  overflow: hidden;
}
.icon-play-circled2{
  font-size: 80px;
  color: #fff;
}

/* Product */
.product {
  padding-top: 120px;
  margin-bottom: 243px;
}
.product__inner {
  display: flex;
  justify-content: space-between;
}
.product__inner__left {
  position: relative;
}
.product__item-foto {
  margin-left: 60px;
  padding-left: 97px;
  padding-right: 114px;
  max-width: 638px;
  max-height: 573px;
  overflow: hidden;
  background-color: rgba($color: #01996D, $alpha: 0.2);
  border-radius: 20px;
}
.product__item__card {
  display: flex;
  align-items: center;
  flex-direction: column;
  max-width: 296px;
  width: 100%;
  max-height: 293px;
  height: 100%;
  background-color: #fff;
  text-align: center;
  border-radius: 4px;
  position: absolute;
  top: 421px;
  z-index: 10;
  box-shadow: 0px 4px 18px 0px #A5A5A540;

}
.p-card__for {
  padding-top: 22px;
  font-size: 14px;
  line-height: 23px;
  margin-bottom: 7px;
}
.p-card__line {
  margin: 0 auto;
  width: 110px;
  height: 1px;
  background-color: var(--color-black);
  margin-bottom: 13px;
}
.p-card__img {
  align-self: center;
  max-width: 60px;
  max-height: 60px;
  border-radius: 50%;
  background-color: rgba($color: #01996D, $alpha: 0.2);
  overflow: hidden;
}
.p-card__name {
  padding-top: 6px;
  font-size: 12px;
  line-height: 19px;
  color: var(--color-black);
}
.p-card__name-list {
  padding-top: 20px;
  font-size: 16px;
  line-height: 25px;
  color: var(--color-black);
  margin-bottom: 16px;
}
.p-card__list {
  display: flex;
  flex-direction: column;
  align-items: start;
  list-style: inherit;
}
.p-card__list__item {
  margin-bottom: 16px;
}
.p-card__list__item:last-child {
  margin-bottom: 0;
}
.product__inner__right {
  padding-left: 15px;
}
.how-it__title {
  max-width: 584px;
}
.p-right__text {
  padding-top: 20px;
  max-width: 530px;
  color: var(--color-text);
  font-size: 20px;
  line-height: 31.4px;
}
.button-product {
  margin-top: 60px;
}

/*Footer*/
.footer {
  padding-top: 95px;
  background-color: var(--background1);
}
.footer__inner__title {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 55px;
  color: #fff;
  margin-bottom: 60px;
}
.footer__inner__list {
  padding-bottom: 144px;
  
}
.footer__list__item {
  margin-bottom: 55px;
}
.footer__list__item:last-child{
  margin-bottom: 0;
}
.footer__link {
  text-decoration: none;
  color: #fff;
  font-size: 25px;
  line-height: 30px; 
}
.footer__link:hover {
  color: #aaa;
}

/*Media*/
@media  screen and (max-width: 1530px) {
  .contain-story {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media  screen and (max-width: 1550px) {
  .hearder-nav__row,
  .contain {
    padding-left: 15px;
    padding-right: 15px;
  }
  .story__inner{
    padding-left: 15px;
    padding-right: 15px;
  }
  .story__left__h2 {
    padding-top: 210px;
  }
}
@media  screen and (max-width: 1250px) {
  .product__item-foto{
    margin-left: 30px;
  }
  .product__item__card{
    top: 350px;
  }
}

@media  screen and (max-width: 1050px) {
  .hearder-nav__row{
    flex-direction: column;
  }
  .row__logo{
    padding-top: 30px;
    padding-bottom: 40px;
  }
  .row__menu1,
  .row__menu2 {
    margin-bottom: 10px;
  }
  .button-header {
    padding-bottom: 200px;
  }
  .how-it__cards {
    flex-direction: column;
    align-items: center;
  }
  .c-item2{
    padding-top: 0px;
  }
  .how-it__cards__item {
    margin-right: 0;
  }
  .story__inner {
    flex-direction: column;
  }
  .story__left__h2 {
    padding-top: 100px;
  }
  .story__right {
    margin-top: 90px;
    margin-bottom: 100px;
  }
  .product {
    margin-bottom: 150px;
  }
  .product__inner{
    flex-direction: column;
    align-items: center;
  }
  .product__inner__right{
    padding-top: 150px;
  }
  .product__item-foto{
    margin-left: 30px;
  }
  .product__item__card{
    top: 350px;
  }
}
@media  screen and (max-width: 850px) {
  .header__p{
    color: var(--color-text-menu);
  }
  .how-it {
    padding-top: 100px;
  }
  .litle-title {
    text-align: center;
  }
  .how-it__content {
    flex-direction: column;
    align-items: center;
  }
  .how-it__title {
    font-size: 36px;
    line-height: 48px;
  }
  .footer {
    padding-top: 60px;
  }
  .footer__inner__list {
    padding-bottom: 90px;
  }
} 
@media  screen and (max-width: 680px) {
  .header__h1{
    font-size: 50px;
  }
  .header__p{
    width: 330px;
  }
  .hearder-nav__row{
    flex-direction: row;
    align-items: flex-start;
  }
  .row__menu1,
  .row__menu2{
    flex-direction: column;
    align-items: center;
    margin-bottom: 0;
  }
  .menu__item-2{
    padding: 13px 30px;
  }
  .product__item-foto{
    margin-left: 30px;
  }
  .product__item__card{
    max-width: 240px;
    max-height: 240px;
    top: 300px;
  }
  .story__left__h2 {
    padding-top: 70px;
    margin-bottom: 15px;
    font-size: 38px;
    line-height: 56px;
  }
  .story__right{
    margin-top: 70px;
    margin-bottom: 70px;
  }
  .p-card__name-list {
  padding-top: 8px;
  margin-bottom: 8px;
  }
  .p-card__for {
    padding-top: 12px;
    margin-bottom: 4px;
  }
  .p-card__line {
    margin-bottom: 8px;
  }
  .p-card__list {
    display: flex;
    flex-direction: column;
    align-items: start;
    list-style: inherit;
  }
  .p-card__list__item {
    margin-bottom: 12px;
  }
}
@media  screen and (max-width: 560px) {
  .product {
    padding-top: 80px;
    margin-bottom: 100px;
  }
  .product__item-foto {
    margin-left: 0px;
  }
  .product__item__card{
    top: 220px;
  }
}
@media  screen and (max-width: 475px) {
  .hearder-nav__row{
    flex-direction: column;
    align-items: flex-start;
  }
  .menu__item-2{
    padding: 13px 39px;
  }
  .row__logo,
  .row__menu1,
  .row__menu2{
    align-self: center;
  }
  .product__inner__left{
    position: static;
  }
  .product__item-foto {
    margin-bottom: 20px;
  }
  .product__item__card{
    position: static;
    margin: 0 auto;
  }
  .product__inner__right {
    padding-top: 70px;
    padding-left: 0;
  }
} 
@media  screen and (max-width: 380px) {
  .how-img {
    height: 400px;
  }
  .header__p {
    max-width: 270px;
  }
}